:root {
  --font-figtree: "figtree", sans-serif;
}

:root {
  --black: hsl(0, 0%, 7%);
  --white: hsl(0, 0%, 100%);
  --grey: hsl(0, 0%, 50%);
  --yellow: hsl(47, 89%, 63%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  max-width: 1440px;
  max-height: 960px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-figtree);
  background-color: var(--yellow);
}

html,
body {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
  font-weight: 800;
  font-size: 1.25rem;
  color: var(--black);
}
@media (min-width: 71.875em) {
  h1,
h2,
h3 {
    font-size: 1.5rem;
  }
}

p {
  margin: 0;
  font-weight: 500;
  font-size: 0.875rem;
  line-height: 1.5;
}
@media (min-width: 71.875em) {
  p {
    font-size: 1rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.wrapper .container {
  display: flex;
  justify-content: start;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 1.5rem;
  max-width: 20.4375rem;
  padding: 1.5rem;
  margin-inline: 1.5rem;
  border-radius: 1.25rem;
  background-color: var(--white);
  box-shadow: 8px 8px var(--black);
}
@media (min-width: 71.875em) {
  .wrapper .container {
    max-width: 24rem;
    margin: 0;
    transition: box-shadow 0.5s ease;
  }
}
.wrapper .container__image img {
  display: block;
  max-width: 100%;
  height: auto;
  border-radius: 0.625rem;
}
.wrapper .container__text {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  row-gap: 0.75rem;
}
@media (min-width: 71.875em) {
  .wrapper .container__text h1:hover {
    color: var(--yellow);
    cursor: pointer;
  }
}
.wrapper .container__text-category {
  padding: 0.25rem 0.75rem;
  text-align: center;
  border-radius: 0.25rem;
  background-color: var(--yellow);
}
.wrapper .container__text-category span {
  font-size: 0.75rem;
  font-weight: 800;
}
@media (min-width: 71.875em) {
  .wrapper .container__text-category span {
    font-size: 0.875rem;
  }
}
.wrapper .container__text time {
  font-size: 0.75rem;
  font-weight: 500;
  color: var(--black);
}
@media (min-width: 71.875em) {
  .wrapper .container__text time {
    font-size: 0.875rem;
  }
}
.wrapper .container__text p {
  color: var(--grey);
}
.wrapper .container__author {
  display: flex;
  justify-content: start;
  align-items: center;
  column-gap: 0.75rem;
}
.wrapper .container__author img {
  display: block;
  max-width: 100%;
  height: auto;
}
.wrapper .container__author span {
  font-size: 0.875rem;
  font-weight: 800;
}
@media (min-width: 71.875em) {
  .wrapper .container:hover {
    box-shadow: 16px 16px var(--black);
  }
}
.wrapper footer {
  margin-top: 0.9375rem;
}
.wrapper footer .attribution {
  font-size: 11px;
  text-align: center;
}
.wrapper footer .attribution a {
  color: #3e52a3;
}